                color: 0xafc0ca
                // opacity: 0.5
            });
            let cube = new THREE.Mesh( cubeGeometry, material );
            cube.position.x = x;
            cube.position.y = y;
            cube.position.z = z;
            cube.name = name;
            this.scene.add(cube);
            // return cube;
        },


        createSphere(name,r, width, height,x,y,z){
            let cubeSphere = new THREE.SphereGeometry(r, width, height);
            let material = new THREE.MeshPhongMaterial({
                color: 0xafc0ca
                // opacity: 0.5
            });
            let cube = new THREE.Mesh( cubeSphere, material );
            cube.position.x = x;
            cube.position.y = y;
            cube.position.z = z;
            cube.name = name;
            this.scene.add(cube);
        },

