        buildMovingCars() {
            let carsPosition = [
                [-650, 825, 0],
                [150, 825, 0],
                [925, 100, 2.5],
                [150, -825, 5],
                [-925, -300, 7.5]
            ]
            carsPosition.forEach(elem => {
                let car = new Car()
                let x = elem[0],
                    z = elem[1],
                    r = elem[2]
                car.setPosition(x, -200, z)
                car.mesh.rotation.y = r * Math.PI
                this.cars.push(car)
                this.scene.add(car.mesh)
            })
        },

        buildStaticCars() {
            let carsPosition = [
                [-84, 82, 1.5],
                [-58, 82, 1.5],
                [-32, 82, 1.5],
                [84, 82, 1.5]
            ]
            carsPosition.forEach(elem => {
                let car = new Car()
                let x = elem[0] * 7.1,
                    z = elem[1] * 7.1,
                    r = elem[2]
                car.setPosition(x, -200, z)
                car.mesh.rotation.y = r * Math.PI
                this.scene.add(car.mesh)
            })
        },

        carMoving(car) {
            let angle = car.mesh.rotation.y
            let x = car.mesh.position.x,
                z = car.mesh.position.z

            if (x < 925 && z === 825) {
                car.forward()
            } else if (angle < 0.5 * Math.PI) {
                car.turnLeft(0.5 * Math.PI, 0.1)
            } else if (x === 925 && z > -825) {
                car.forward()
            } else if (angle < Math.PI) {
                car.turnLeft(0.5 * Math.PI, 0.1)
            } else if (x > -925 && z == -825) {
                car.forward()
            } else if (angle < 1.5 * Math.PI) {
                car.turnLeft(0.5 * Math.PI, 0.1)
            } else if (x === -925 && z < 825) {
                car.mesh.rotation.y = 1.5 * Math.PI
                car.forward()
            } else if (angle < 2 * Math.PI) {
                car.turnLeft(0.5 * Math.PI, 0.1)
            } else {
                car.setPosition(-925, -200, 825)
                car.mesh.rotation.set(0, 0, 0)
            }
        },

        buildRoad() {
            let road = new THREE.Object3D()
            let roadColor = 0xffffff
            //外边框
            let roadBorderOuterCoords = [
                [-970, 870],
                [970, 870],
                [970, -870],
                [-970, -870]
            ]
            let roadBorderOuterHoleCoords = [
                //内边框
                [-859, 759],
                [-859, -759],
                [859, -759],
                [859, 759]
            ]
            let roadBorderOuterShape = utils.makeShape(roadBorderOuterCoords, roadBorderOuterHoleCoords)
            let roadBorderOuterGeometry = utils.makeExtrudeGeometry(roadBorderOuterShape, 0.1)
            let roadBorderOuter = utils.makeMesh('phong', roadBorderOuterGeometry, 0x333333)
            road.add(roadBorderOuter)

            let roadBorderInnerCoords = [
            //小区范围
                [-786, 655],
                [-786, -655],
                [786, -655],
                [786, 655],
                [95, 655],
                [95, 595],
                [695, 595],
                [695, -595],
                [-695, -595],
                [-695, 595],
                [-95, 595],
                [-95, 655]
            ]
            let roadBorderInnerShape = utils.makeShape(roadBorderInnerCoords)
            let roadBorderInnnerGeometry = utils.makeExtrudeGeometry(roadBorderInnerShape, 0.5)
            let roadBoaderInnder = utils.makeMesh('phong', roadBorderInnnerGeometry, roadColor)
            roadBoaderInnder.rotation.y = Math.PI
            road.add(roadBoaderInnder)

            let roadLinesGeometry = new THREE.Geometry()
            let roadLineGeometry = new THREE.BoxGeometry(50, 0.3, 6)

            let roadLinesBottomGeometry = new THREE.Geometry()
            for (let i = 0; i < 20; i++) {
                let geometry = roadLineGeometry.clone()
                geometry.translate(i * 70, 0, 0)
                roadLinesBottomGeometry.merge(geometry)
            }
            roadLinesBottomGeometry.translate(-650, 0, 800)
            roadLinesGeometry.merge(roadLinesBottomGeometry)

            let roadLinesTopGeometry = roadLinesBottomGeometry.clone()
            roadLinesTopGeometry.translate(0, 0, -1600)
            roadLinesGeometry.merge(roadLinesTopGeometry)

            let roadLinesLeftGeometry = roadLinesBottomGeometry.clone()
            roadLinesLeftGeometry.rotateY(0.5 * Math.PI)
            roadLinesLeftGeometry.translate(100,0,0);//x轴偏移
            roadLinesGeometry.merge(roadLinesLeftGeometry)

            let roadLinesRightGeometry = roadLinesBottomGeometry.clone()
            roadLinesRightGeometry.rotateY(-0.5 * Math.PI)
            roadLinesRightGeometry.translate(-100,0,0);//x轴偏移
            roadLinesGeometry.merge(roadLinesRightGeometry)
            roadLinesGeometry = new THREE.BufferGeometry().fromGeometry(roadLinesGeometry)
            let roadLines = utils.makeMesh('phong', roadLinesGeometry, 0xffffff)
            road.add(roadLines)
            road.position.y = -200;
            this.scene.add(road)
        },

