                frontMain.castShadow = true
                frontMain.receiveShadow = true
                hospital.add(frontMain)

                let frontTopShape = frontMainShape
                let frontTopGeometry = utils.makeExtrudeGeometry(frontTopShape, 5)
                let frontTop = utils.makeMesh('lambert', frontTopGeometry, 0xb1a7af)
                frontTop.position.y = 400
                hospital.add(frontTop)

                
                return hospital
            }

            function createWindow() {
                let windowObj = new THREE.Object3D()
                // let glassGeometry = new THREE.PlaneGeometry(20, 20)
                let glassGeometry = new THREE.BoxBufferGeometry(20, 20,2)
                let glass = utils.makeMesh('phong', glassGeometry, 0x6a5e74)
                windowObj.add(glass)

                let windowBorderGeometry = new THREE.BoxBufferGeometry(22, 2, 2)
                let windowBorder = utils.makeMesh('phong', windowBorderGeometry, 0xffffff)

                let windowBorderTop = windowBorder.clone()
                windowBorderTop.position.y = 10
                windowObj.add(windowBorderTop)

                let windowBorderBottom = windowBorder.clone()
                windowBorderBottom.position.y = -10
                windowObj.add(windowBorderBottom)

                let windowBorderLeft = windowBorder.clone()
                windowBorderLeft.rotation.z = 0.5 * Math.PI
                windowBorderLeft.position.x = -10
                windowObj.add(windowBorderLeft)

                let windowBorderRight = windowBorderLeft.clone()
                windowBorderRight.position.x = 10
                windowObj.add(windowBorderRight)

                return windowObj
            }

            function createTree(x, y, z) {
                x = x || 0
                y = y || 0
                z = z || 0

                let tree = new THREE.Object3D()

                let treeTrunkGeometry = new THREE.BoxBufferGeometry(2, 16, 2)
                let treeTrunk = utils.makeMesh('lambert', treeTrunkGeometry, 0x8a613a)
                treeTrunk.position.y = 8
                tree.add(treeTrunk)

                let treeLeafsGeometry = new THREE.BoxBufferGeometry(8, 8, 8)
                let treeLeafs = utils.makeMesh('lambert', treeLeafsGeometry, 0x9c9e5d)
                treeLeafs.position.y = 13
                tree.add(treeLeafs)

                tree.position.set(x, y, z)

                return tree
            }
        },

        addMonitor() {
            //为墙面安装摄像头
            let loader=new THREE.TextureLoader();
            let _this = this;
            loader.load('static/images/sxt_icon.png',function(texture){
                let monitorgeometry = new THREE.BoxGeometry(80, 80, 2);
                let monitormaterial = new THREE.MeshBasicMaterial({map:texture});
                monitormaterial.opacity=1.0;
                monitormaterial.transparent=true;
                let monitor = new THREE.Mesh( monitorgeometry,monitormaterial);
                monitor.position.set(250,-20,430);
                monitor.name = 'sxt';
                _this.scene.add(monitor);
            });
        },
        addText() {
            //提示文字
            let loader=new THREE.TextureLoader();
            let _this = this;
            loader.load('static/images/text.png',function(texture){
                let monitorgeometry = new THREE.BoxGeometry(150, 80, 2);
                let monitormaterial = new THREE.MeshBasicMaterial({map:texture});
                monitormaterial.opacity=1.0;
                monitormaterial.transparent=true;
                let monitor = new THREE.Mesh( monitorgeometry,monitormaterial);
                monitor.position.set(85,-130,430);
                monitor.name = 'text';
                _this.scene.add(monitor);
            });
        },

        //创建3D字体
        createText() {
            let _this = this;
            let text = new THREE.FontLoader().load('static/font/Microsoft YaHei_Regular.json', function(text) {
                let gem = new THREE.TextGeometry('爵无锦有', {
                    size: 30, //字号大小，一般为大写字母的高度
                    height: 10, //文字的厚度
                    weight: 'normal', //值为'normal'或'bold'，表示是否加粗
                    font: text, //字体，默认是'helvetiker'，需对应引用的字体文件
                    style: 'normal', //值为'normal'或'italics'，表示是否斜体
                    bevelThickness: 1, //倒角厚度
                    bevelSize: 1, //倒角宽度
                    curveSegments: 30,//弧线分段数，使得文字的曲线更加光滑
                    bevelEnabled: true //布尔值，是否使用倒角，意为在边缘处斜切
                });
                // gem.center();
                let mat = new THREE.MeshPhongMaterial({
                    color: 0xffe502,
                    specular: 0x009900,
                    shininess: 30,
                    shading: THREE.FlatShading
                });
                let textObj = new THREE.Mesh(gem, mat);
                textObj.castShadow = true;
                textObj.position.x = 0;
                textObj.position.y = -70;
                textObj.position.z = 430;
                textObj.rotation.x = 0;
                textObj.rotation.y = Math.PI * 2;
                _this.scene.add(textObj);
            });
        },

        createSpriteText2(text,x,y,z){
            let canvas = document.createElement('canvas');
            canvas.width=256;
            canvas.height=128;
            let ctx = canvas.getContext('2d');
            ctx.fillStyle = '#bbf42f';
            ctx.font = 'Bold 70px Arial';
            ctx.lineWidth = 4;
            ctx.fillText(text,4,104);
            let texture = new THREE.Texture(canvas);
            texture.needsUpdate = true;

            let material = new THREE.SpriteMaterial({map:texture});
            let textObj = new THREE.Sprite(material);
            textObj.scale.set(0.5 * 200, 0.25 * 200, 0.75 * 200);
            textObj.position.set(x,y,z);
            this.scene.add( textObj );
        },
        createSpriteText3(text,x,y,z){
            let canvas = document.createElement('canvas');
            canvas.width=512;
            canvas.height=256;
            let ctx = canvas.getContext('2d');
            ctx.fillStyle = '#bbf42f';
            ctx.font = 'Bold 100px Arial';
            ctx.lineWidth = 4;
            ctx.fillText(text,4,204);
            let texture = new THREE.Texture(canvas);
            texture.needsUpdate = true;

            let material = new THREE.SpriteMaterial({map:texture});
            let textObj = new THREE.Sprite(material);
            textObj.scale.set(0.5 * 200, 0.25 * 200, 0.75 * 200);
            textObj.position.set(x,y,z);
            this.scene.add( textObj );
        },

        initText(){
            this.createSpriteText2('1栋',-530,120,480);
            this.createSpriteText2('2栋',600,20,380);
            this.createSpriteText2('3栋',300,20,150);
            this.createSpriteText2('5栋',300,20,-70);
            this.createSpriteText2('4栋',600,20,150);
            this.createSpriteText2('6栋',600,120,-70);
            this.createSpriteText2('8栋',600,120,-270);
            this.createSpriteText2('9栋',600,120,-450);
            this.createSpriteText2('10栋',300,120,-450);
            this.createSpriteText2('11栋',0,120,-450);
            this.createSpriteText2('7栋',300,20,-250);
            this.createSpriteText3('群众服务楼',-140,190,30);
            this.createSpriteText3('活动中心',-100,250,-300);
            this.createSpriteText2('15栋',-550,120,270);
            this.createSpriteText2('14栋',-550,120,70);
            this.createSpriteText2('13栋',-550,120,-160);
            this.createSpriteText2('12栋',-550,120,-400);
        },


        //7.初始化OBJ对象
        initObject() {
            // this.createFloor();
            this.createFloor2();
            // this.createFloor3();
            this.createLayout();
            this.createText();
            this.initText();
            this.addMonitor();
            this.addText();
        },


        onDocumentMouseMove(event) {
            event.preventDefault();
            var vector = new THREE.Vector3();//三维坐标对象
            vector.set(
                ( event.clientX / window.innerWidth ) * 2 - 1,
                - ( event.clientY / window.innerHeight ) * 2 + 1,
                0.5 );
            vector.unproject( this.camera );
            var raycaster = new THREE.Raycaster(this.camera.position, vector.sub(this.camera.position).normalize());
            var intersects = raycaster.intersectObjects(this.scene.children);
            if (intersects.length > 0) {
                var selected = intersects[0];//取第一个物体
                // console.log("x坐标:"+selected.point.x);
                // console.log("y坐标:"+selected.point.y);
                // console.log("z坐标:"+selected.point.z);
            }
            // event.preventDefault();
            // this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
            // this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
        },

        render() {
            // requestAnimationFrame(this.render);
            this.renderer.render(this.scene, this.camera);
            // this.renderer.autoClear = false;

            this.raycaster.setFromCamera(this.mouse, this.camera);
            let intersects = this.raycaster.intersectObjects(this.scene.children);
            if (intersects.length > 0) {
                if (this.INTERSECTED != intersects[0].object) {
                    if (this.INTERSECTED) this.INTERSECTED.material.color.setHex(this.INTERSECTED.currentHex);
                    this.INTERSECTED = intersects[0].object;
                    this.INTERSECTED.currentHex = this.INTERSECTED.material.color.getHex();
                    this.INTERSECTED.material.color.set( 0xBED264 );
                }
            } else {
                if (this.INTERSECTED) this.INTERSECTED.material.color.set(this.INTERSECTED.currentHex);
                this.INTERSECTED = null;
            }
        },
        

        animate(){
            this.cars.forEach(car => {
                this.carMoving(car)
            })
            requestAnimationFrame( this.animate );
            this.renderer.render( this.scene, this.camera );
            TWEEN.update( );
            this.update();
            this.render();
        },

        update(){
            let delta = this.clock.getDelta();
            let moveDistance = 200 * delta;
            let rotateAngle = Math.PI / 2 * delta;
            this.controls.update();
        },
        

        //初始化函数
        init(){
            this.initScene();
            this.initCamera();
            this.initRender();
            this.initEvent();
            this.initControls();
            this.initLight();
            this.initObject();
            // this.render();
            // this.objGroup123();
            this.buildRoad()
            this.buildbuilding();
            
            this.buildMovingCars();
            this.buildStaticCars();

        }

    },
    components: {

    }

}

</script>
