
import Layout from '@/pages/Layout'
import Lxc from '@/pages/map/lxc'

let routes = [
    {
        path: '/',
        component: Layout,
        redirect: '/map',
        children: [{
            path: '/map',
            component: Lxc
        }]
    }
    
];

export default routes;



